<template>
  <div class="container">
    <ul class="submenu">
      <li><a :href="''"><i class="fa fa-telegram"/></a></li>
      <li><a :href="''"><i class="fa fa-vk"/></a></li>
      <!--<li><a @click="setSoon"><i class="fa fa-instagram"/></a></li>-->
    </ul>
  </div>
</template>

<script>
export default {
  name: 'SocialLinks',
  data () {
    return {
      vkLink: 'https://vk.me/join/AJQ1dxOvtBUuRzwPao005HzV',
      telegramLink: 'https://t.me/joinchat/E53qM0ofa2Sw0EaH7k70kA'
    }
  },
  methods: {
    setSoon () {
      this.$emit('set-soon')
    }
  }
}
</script>

<style lang="scss" scoped>
@import url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');
body{
  background-color: #ccc;
}
.container {
  text-align: center;
  margin-top:25px;
  max-height: 15%;
}

ul.submenu {
  background-color: transparent;
  border-radius: 5px;
  display: inline-block;
  padding: 4px 10px;
  margin: 0;
  overflow: hidden;
  li {
    display: inline-block;
    a {
      i.fa {
        padding: 10px;
        display: inline-block;
        border: 1px solid transparent;
        color: #ccc;
        height: 30px;
        width: 30px;
        border-radius: 50px;
        margin: 10px;
        text-align: center;
        line-height: 30px;
        position: relative;
        z-index: 2;
        font-size: 30px;
        &::after {
          content: "";
          height: 0%;
          width: 0%;
          position: absolute;
          top: -500%;
          bottom: -500%;
          left: -500%;
          right: -500%;
          margin: auto;
          border: 0px solid transparent;
          border-radius: 200px;
          opacity: 0;
          z-index: -1;
          transition: all ease 0.5s;
        }
      }
      &:hover {
        i.fa::before {
          color: #fff;
        }
        i.fa-vk::after {
          height: 200%;
          width: 200%;
          background: url('../images/arm-flag.png') no-repeat;
          border-radius: 0px;
          opacity: 1;
        }

        i.fa-telegram::after {
          height: 200%;
          width: 200%;
          background: url('../images/arm-flag.png') no-repeat;
          border-radius: 0px;
          opacity: 1;
        }

        i.fa-instagram::after {
          height: 200%;
          width: 200%;
          background: url('../images/arm-flag.png') no-repeat;
          border-radius: 0px;
          opacity: 1;
        }
      }
    }
  }
}
</style>



