<template>
  <div class="container">
    <div class="bg">
      <h1 class="before-all">Welcome to</h1>
      <h1 class="main-title">{{ title }}</h1>
      <img src="../images/yarhushta.png" alt="">
      <div class="overlay">
        <h1 v-if="isSoon" class="main-title soon">{{ soon }}</h1>
        <h1 class="title">{{ title }}</h1>
        <main-info-block class="main-block"/>
      </div>
    </div>
  </div>
</template>

<script>
import MainInfoBlock from './MainInfoBlock'

export default {
  name: 'BusinessCard',
  data () {
    return {
      title: 'Armenian Youth',
      soon: 'Link coming soon...',
      isSoon: false
    }
  },
  components: {
    MainInfoBlock
  }
}
</script>

<style lang="scss" scoped>
  $bg: rgba(0, 0, 0, 0.2);
  .main-title {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
    color: white;
  }
  .soon {
    color: red;
    z-index: 2;
  }
  .main-block {
    z-index: 1;
    height: 87%;
  }
  .before-all {
    width: 100%;
    text-align: center;
    color: white;
    top: 40%;
    position: absolute;
  }
  .title {
    margin-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid white;
  }

  *,
  *:before,
  *:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  .container {
    position: relative;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    width: 420px;
  }
  .bg,
  .overlay {
    text-align: center;
  }

  img,
  .overlay {
    transition: .3s all;
    border-radius: 3px;
  }

  .bg {
    float: left;
    position: relative;
    margin: .5%;
    img {
      width: 100%;
      margin-bottom: -4px;
    }
    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 100%;
      background: $bg;
      color: #fff;
      height: 600px;
      opacity: 0;
    }
    &:hover {
      .overlay {
        opacity: 1;
      }
      img {
        -webkit-filter: blur(5px);
        filter: blur(5px);
      }
    }
  }

</style>



