<template>
  <div>
    <div class="main-info relative h-100 center-align">
      <div class="block w-100">
        <div :class="block.class">
          <div class="title">{{ block.title }}</div>
          <p class="text"> {{ block.mainText }}</p>
          <div class="button-blocks" :class="{'flex': block.nextBlock && block.prevBlock}">
            <social-links v-if="activeBlock === 'howToJoin'" class="link-list"/>
            <div :class="createClass(block, 'prev')" class="button relative" v-if="block.prevBlock" @click="goToPrevBlock(block.prevBlock)">
              <div class="button-text">{{ prevButtonText(block.prevBlock) }}</div>
            </div>
            <div :class="createClass(block, 'next')" class="button relative" v-if="block.nextBlock" @click="goToNextBlock(block.nextBlock)">
              <div class="button-text">{{ block.buttonText }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SocialLinks from './SocialLinks'
export default {
  name: 'MainInfoBlock',
  components: {
    SocialLinks
  },
  data () {
    return {
      activeBlock: 'who',
      who: {
        title: 'Итак, кто мы такие?',
        mainText: 'Мы группа людей, движимая идеей объединения армянской молодежи c общими интересами. Для чего? Да всё просто – чтобы вдали от Родины продолжать общаться с соотечественниками. Это основная причина создания Armenian Youth!',
        buttonText: 'Кого мы ищем?',
        nextBlock: 'whom',
        class: 'who-block'
      },
      whom: {
        title: 'Кого мы ищем?',
        mainText: 'Людей со схожими желаниями, которые хотят развиваться и помогать в этом другим. Если у тебя есть крутые идеи или умения, и ты можешь красиво и лаконично доносить информацию до других, а самое главное – хочешь создать нечто потрясающее совместными усилиями, тогда присоединяйся к нашему сообществу!',
        buttonText: 'Для чего мы ищем?',
        nextBlock: 'forWhat',
        prevBlock: 'who',
        class: 'whom-block'
      },
      forWhat: {
        title: 'Для чего мы ищем?',
        mainText: 'Для создания информационного портала, где будем общаться, обмениваться знаниями и ценными навыками!',
        buttonText: 'Как к нам присоединиться?',
        nextBlock: 'howToJoin',
        prevBlock: 'whom',
        class: 'for-what-block'
      },
      howToJoin: {
        title: 'Как к нам присоединиться?',
        mainText: 'Для того чтобы присоединиться, перейдите по одной из ссылок',
        prevBlock: 'forWhat',
        class: 'how-to-join-block'
      }
    }
  },
  computed: {
    block () {
      return this[this.activeBlock]
    }
  },
  methods: {
    createClass (block, navigation = '') {
      return !block.nextBlock || !block.prevBlock ? this.activeBlock + ' button-center' : this.activeBlock + ' button-' + navigation
    },
    goToNextBlock (next) {
      this.activeBlock = next
    },
    goToPrevBlock (prev) {
      this.activeBlock = prev
    },
    prevButtonText (prev) {
      return this[prev].title
    }
  }
}
</script>

<style lang="scss" scoped>
.link-list {
  width: 100%;
}
.link {
  display: inline-block;
}
.main-info {
  .block {
    .title {
      font-size: 30px;
      margin-bottom: 15px;
    }
    .text {
      text-align: justify;
      margin: 0 15px;
      text-indent: 15px;
      font-size: 20px;
    }
    .button-next {
      width: 40%;
    }
    .button-text {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    .button-prev {
      width: 40%;
    }
    .button-blocks {
      justify-content: center;
    }
    .button {
      border: 1px solid black;
      background-color: #FFA100;
      border-radius: 200px;
      margin: 25px 15px;
      height: 40px;
    }
    .button-center {
      display: block;
      text-align: center;
    }
  }
}
</style>



